const Dropdown = () => {
  const $currentLang = $('.js-lang');
  const $changeLang = $('.js-lang-hidden');
  const $arrow = $('.js-arrow');
  const $listToggler = $('.js-list-toggler');
  const $list = $('.js-list');

  init();

  function init() {
    $currentLang.on('click', toggleList);
    $listToggler.on('click', toggleList)
  }

  function toggleList  () {
    $(this).find($changeLang).slideToggle();
    $(this).find($arrow).toggleClass('is-rotated');
    $list.children().slideToggle();
  }
};

export default Dropdown;