const Tabs = () => {
  const $tab = $('.tab-link');
  const $tabContent = $('.tab-content');

  init();

  function init () {
    $tab.on('click', showTab)
  }

  function showTab() {
    let $tabId = $(this).attr('data-tab');
    $tab.removeClass('is-active');
    $tabContent.removeClass('is-active');

    $(this).addClass('is-active');
    $("#"+$tabId).addClass('is-active');
  }
};

export default Tabs;